.container {
    padding: 20px;
    width: 500px;
    margin: 0 auto;
}

.questionHeader {
    text-align: left;
    color: grey;
    margin-bottom: 10px;
}

.questionText {
    font-weight: bold;
    margin-bottom: 20px;
    text-align: left;
}

.answerList {
    list-style-type: none;
    padding-left: 0;
}

.answerItem {
    margin-bottom: 10px;
    color: black;
}

.answerButton {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid grey;
    border-radius: 5px;
    text-align: left;
    display: block;
}

.nextButton {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: green;
    color: white;
    border: none;
    border-radius: 5px;
    display: flex;
}

.resultText {
    margin-top: 20px;
    font-size: 18px;
    color: green;
}

.progressContainer {
    display: flex;
    margin-top: 20px;
}

.progressCircle {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: lightgrey;
    display: flex;
    justify-content: center;
    margin: 0 5px;
    font-size: 18px;
    color: white;
    font-weight: bold;
    border: 2px solid transparent;
}

.greenCircle {
    background-color: green;
    color: white;
}
